<template>
	<div>
		<Search></Search>
		<div class="container">
			<div class="bread-crumb">
				<Breadcrumb>
					<BreadcrumbItem to="/">
						<Icon type="ios-home-outline"></Icon>
						首页
					</BreadcrumbItem>
					<BreadcrumbItem>
						<Icon type="ios-search-outline"></Icon>
						{{searchKey}}
					</BreadcrumbItem>
				</Breadcrumb>
			</div>
			<!-- 商品展示容器 -->
			<div class="goods-box">
				<div class="goods-list-box">
					<div class="goods-list-tool">
						<ul>
							<li v-for="(item,index) in sortTag" :key="index" @click="orderBy(index)">
								<span :class="{ 'goods-list-tool-active': isAction[index]}">{{item}}</span>
							</li>
						</ul>
					</div>
					<div class="goods-list">
						<div class="product-not-found-container" v-if="searchResult.GoodsModel.length === 0">
							<div class="product-not-found-img">
								<img src="../../static/img/goodsList/notfound.png" width="200" height="200">
							</div>
							<div class="product-not-found-txt">未找到相关商品</div>
						</div>
						<div class="goods-show-info" v-for="(item, index) in searchResult.GoodsModel" :key="index">
							<router-link :to="`/goodsDetail?id=${item.GoodsID}`">
								<div class="goods-show-img">
									<img :src="item.Image" width="220" height="220"/>
								</div>
								<div class="goods-show-price">
				                <span>
				                    <span class="seckill-price-icon text-danger">￥</span>
				                    <span class="seckill-price text-danger">{{item.SpecialOffer}}</span>
					                <span v-if="item.OriginalPrice !== 0" style="color: #999999; font-size: 14px"><s>￥{{item.OriginalPrice}}</s></span>
				                </span>
								</div>
								<div class="goods-show-detail">
									<span>{{item.GoodsName | ellipsis(14)}}</span>
								</div>
								<div style="font-size: 12px; display: flex; justify-content: flex-end; color: black;">
									<span style="margin-right: 20px;">月售：{{item.SalesVolumes}}</span>
									<span>总售：{{item.TotalSalesVolumes}}</span>
								</div>
							</router-link>
						</div>
					</div>
				</div>
			</div>
			<div class="goods-page">
				<Page :total="searchResult.PageCount" :page-size="24" :current.sync="searchPage" show-total
				      @on-change="changePage"></Page>
			</div>
		</div>
		<Footer></Footer>
	</div>
</template>

<script>
	import Search from './Search';
	import Footer from './footer/Footer';
	import {mapActions, mapMutations, mapState} from 'vuex';

	export default {
		name: 'GoodsList',

		components: {
			Search,
			Footer
		},

		data() {
			return {
				isAction: [true, false, false],
				isASC: [true, true, true],
				sortTag: ['上架时间', '价格', '综合'],
			};
		},

		created() {
		},

		mounted() {
			this.SET_SEARCH_PAGE(1);
			this.SET_GOODS_ORDER_BY(1);
		},

		beforeRouteEnter(to, from, next) {
			window.scrollTo(0, 0);
			next();
		},

		computed: {
			...mapState(['searchResult', 'searchKey', 'sortKey', 'searchCategory', 'searchPage']),
		},

		methods: {
			...mapActions(['loadSearchResult']),
			...mapMutations(['SET_GOODS_ORDER_BY', 'SET_SEARCH_PAGE']),

			async orderBy(index) {
				let sKey;
				if (this.isAction[index]) {
					sKey = (this.sortKey % 2 > 0) ? this.sortKey + 1 : this.sortKey - 1;
					this.isASC[index] = sKey % 2 > 0;
				} else {
					this.isAction = [false, false, false];
					this.isAction[index] = true;
					sKey = index * 2 + 1;
				}
				this.SET_SEARCH_PAGE(1);
				this.SET_GOODS_ORDER_BY(sKey);
				await this.loadSearchResult([this.searchKey, 0, this.searchCategory]);
			},

			async changePage(id) {
				this.SET_SEARCH_PAGE(id);
				await this.loadSearchResult([this.searchKey, id - 1, this.searchCategory]);
			}
		},
	};
</script>

<style scoped>
	.container {
		margin: 15px auto;
		width: 85%;
		min-width: 1000px;
	}

	.bread-crumb {
		margin-bottom: 15px;
	}

	.text-danger {
		color: #A94442;
	}

	.seckill-price {
		margin-right: 5px;
		font-size: 25px;
		font-weight: bold;
	}

	.seckill-price-icon {
		font-size: 20px;
		font-weight: bold;
	}

	.goods-box {
		display: flex;
	}

	/* ---------------侧边广告栏开始------------------- */
	.item-as-title span:first-child {
		margin-left: 20px;
	}

	.item-as-title span:last-child {
		float: right;
		margin-right: 15px;
		font-size: 10px;
		color: #ccc;
	}

	.item-as-price span {
		font-size: 18px;
	}

	.item-as-selled span {
		color: #005AA0;
	}

	/* ---------------侧边广告栏结束------------------- */

	/* ---------------商品栏开始------------------- */
	.goods-list-box {
		width: 100%;
	}

	.goods-list-tool {
		width: 100%;
		height: 38px;
		border: 1px solid #ccc;
		background-color: #F1F1F1;
	}

	.goods-list-tool ul {
		padding-left: 15px;
		list-style: none;
	}

	.goods-list-tool li {
		cursor: pointer;
		float: left;
	}

	.goods-list-tool span {
		padding: 5px 8px;
		border: 1px solid #ccc;
		border-left: none;
		line-height: 36px;
		background-color: #fff;
	}

	.goods-list-tool span:hover {
		border: 1px solid #E4393C;
	}

	.goods-list-tool i:hover {
		color: #E4393C;
	}

	.goods-list-tool-active {
		color: #fff;
		border-left: 1px solid #ccc;
		background-color: #E4393C !important;
	}

	.goods-list {
		display: flex;
		flex-wrap: wrap;
		justify-content: flex-start;
		width: 95%;
		margin: auto;
	}

	.goods-show-info {
		width: 240px;
		padding: 10px;
		margin: 15px 5px;
		border: 1px solid #fff;
		cursor: pointer;
	}

	.goods-show-info:hover {
		border: 1px solid #ccc;
		box-shadow: 0px 0px 15px #ccc;
	}

	.goods-show-price {
		margin-top: 6px;
	}

	.goods-show-detail {
		font-size: 14px;
		margin: 6px 0px;
		color: black;
	}

	.goods-show-num {
		font-size: 12px;
		margin-bottom: 6px;
		color: #009688;
	}

	.goods-show-num span {
		color: #005AA0;
		font-weight: bold;
	}

	.goods-show-seller {
		font-size: 12px;
		color: #E4393C;
	}

	.goods-page {
		margin-top: 20px;
		display: flex;
		justify-content: flex-end;
	}

	/* ---------------商品栏结束------------------- */

	.product-not-found-container {
		margin: 15px auto;
		width: 85%;
	}

	.product-not-found-img {
		margin-top: 15px;
		margin-bottom: 15px;
		text-align: center;
	}

	.product-not-found-txt {
		text-align: center;
		font-size: 20px;
	}
</style>



